      <div class="form-label-group mb-3">
        <label for="passwordCheck">Password Check</label>
        <input
          id="passwordCheck"
          v-model="passwordCheck"
          name="passwordCheck"
          type="password"
          class="form-control"
          placeholder="password-check"
          required
        />
      </div>

      <button
        class="btn btn-lg btn-primary btn-block mb-3"
        type="submit"
        :disabled="isProcessing"
      >Submit</button>

      <div class="text-center mb-3">
        <p>
          <router-link to="/signin">Sign In</router-link>
        </p>
      </div>

      <p class="mt-5 mb-3 text-muted text-center">&copy; 2017-2018</p>
    </form>
  </div>
</template>

<script>
import authorizationAPI from "./../apis/authorization";
import { Toast } from "./../utils/helpers";

export default {
  name: "SignUp",
  data() {
    return {
      name: "",
      email: "",
      password: "",
      passwordCheck: "",
      isProcessing: false
    };
  },
  methods: {
    async handleSubmit(e) {
      try {
        // å¦æ nameãemailãpasswordCheck æ password çºç©ºï¼åä½¿ç¨ Toast æç¤º
        // ç¶å¾ return ä¸ç¹¼çºå¾å¾å·è¡
        if (
          !this.name ||
          !this.email ||
          !this.password ||
          !this.passwordCheck
        ) {
          Toast.fire({
            type: "warning",
            title: "è«å¡«å¥ nameãemail å password"
          });
          return;
        }

        // å¦æ password å passwordCheck ä¸ç¸åï¼åä½¿ç¨ Toast æç¤º
        // ç¶å¾ return ä¸ç¹¼çºå¾å¾å·è¡
        if (this.password !== this.passwordCheck) {
          // å°å¯ç¢¼æ¬ä½æ¸ç©º
          this.password = "";
          this.passwordCheck = "";
          Toast.fire({
            type: "warning",
            title: "è«ç¢ºèª password å password Check æ¯å¦ç¸å"
          });
          return;
        }

        this.isProcessing = true;

        const response = await authorizationAPI.signUp({
          name: this.name,
          email: this.email,
          password: this.password,
          passwordCheck: this.passwordCheck
        });

        const { data, statusText } = response;

        if (statusText !== "OK" || data.status !== "success") {
          throw new Error(statusText);
        }

        this.$router.push("/signin");
      } catch (error) {
        // å°å¯ç¢¼æ¬ä½æ¸ç©º
        this.password = "";
        this.passwordCheck = "";
        // é¡¯ç¤ºé¯èª¤æç¤º
        Toast.fire({
          type: "warning",
          title: "æ¨è¼¸å¥çä¿¡ç®±å·²ç¶è¨»å"
        });
        this.isProcessing = false;
      }
    }
  }
};
</script>
